			// 使用挂载方法获取用户数据
			var userInfo = that.getGlobalUser("ele_login");
			if (userInfo != null) {
				that.userIsLogin = true;
				that.userInfo = userInfo;
			} else {
				that.userIsLogin = false;
				that.userInfo = {};
			}
		}
	}
